@font-face {
  font-family: "Inter";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/Inter-Medium.woff2");
}
@font-face {
  font-family: "Inter Bold";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/Inter-Bold.woff2");
}
@font-face {
  font-family: "Inter Black";
  font-weight: 900;
  font-style: normal;
  src: url("../fonts/Inter-Black.woff2");
}
:root {
  --leading-loose: 34px;
  --container: 1152px;
}

.wp-block-buttons > .wp-block-button.is-style-small-button {
  position: relative;
  display: flex;
  align-items: center;
}
.wp-block-buttons > .wp-block-button.is-style-small-button::after {
  background-color: var(--wp--preset--color--secondary);
  content: "";
  display: block;
  height: 18px;
  width: 18px;
  mask-image: url(data:image/svg+xml;base64,);
  mask-repeat: no-repeat;
  mask-size: 100%;
  margin-top: 3px;
  transition: all 0.3s ease-out;
  transform: translateX(0);
  position: initial;
}
.wp-block-buttons > .wp-block-button.is-style-small-button .wp-block-button__link {
  background: transparent;
  color: var(--wp--preset--color--secondary);
  padding-left: 0;
  padding-right: 16px;
}
.wp-block-buttons > .wp-block-button.is-style-small-button:hover::after {
  transform: translateX(-10px);
}

.wp-site-blocks {
  max-width: var(--container);
  margin: 0 auto;
}

h1 strong,
h2 strong,
h3 strong,
h4 strong {
  color: var(--wp--preset--color--tertiary);
}

p {
  line-height: var(--leading-loose);
}
p strong {
  font-family: var(--wp--preset--font-family--inter-bold);
}

.alignwide,
.alignfull {
  margin: 0 calc(50% - 50vw);
  max-width: 100vw;
  width: 100vw;
}

.pattern-hero,
.pattern-headline-with-cards {
  margin: 100px 0;
}

.float-left-width-30 {
  float: left;
  max-width: 33.3333%;
  padding: 32px;
}

.float-left-width-50 {
  float: left;
  max-width: 50%;
  padding: 32px;
}

.float-right-width-30 {
  float: right;
  max-width: 33.3333%;
  padding: 32px;
}

.float-right-width-50 {
  float: right;
  max-width: 50%;
  padding: 32px;
}

.float-none {
  float: none;
}

.footer {
  margin-top: 50px;
  padding-bottom: 50px;
}
.footer .wp-block-social-links.has-large-icon-size .wp-block-social-link-anchor svg {
  width: 36px;
  height: 36px;
}

.single .wp-block-template-part {
  position: relative;
}
.single .wp-block-template-part .header {
  position: absolute;
  width: 100%;
  z-index: 10;
  filter: brightness(200%);
}
.single .wp-block-cover.post-hero .wp-block-cover__inner-container {
  max-width: var(--container);
  margin: auto;
}
.single .wp-block-cover.post-hero .wp-block-cover__inner-container .wp-block-group {
  display: inline-flex;
  align-items: center;
}
.single .wp-block-cover.post-hero .wp-block-cover__inner-container .wp-block-group .wp-block-post-author__avatar .avatar {
  border-radius: 9999px;
}
.single .wp-block-cover.post-hero .wp-block-cover__inner-container .wp-block-group .wp-block-post-author__content {
  display: flex;
  align-items: center;
}
.single .wp-block-cover.post-hero .wp-block-cover__inner-container .wp-block-group .wp-block-post-author__content .wp-block-post-author__name {
  color: var(--wp--preset--color--primary);
}
.single .wp-block-cover.post-hero .wp-block-cover__inner-container .wp-block-group .wp-block-post-date time {
  color: var(--wp--preset--color--primary);
}
.single .wp-block-cover.post-hero .wp-block-post-terms a {
  color: var(--wp--preset--color--primary);
  text-decoration: none;
}
.single .wp-block-cover.post-hero .wp-block-post-title {
  color: var(--wp--preset--color--primary);
  margin-top: 0;
  margin-bottom: 0;
}

.pattern-blockquote {
  position: relative;
  padding: 25px;
  max-width: 620px !important;
  margin: 80px auto;
}
.pattern-blockquote p {
  font-family: var(--wp--preset--font-family--inter-bold);
  font-size: var(--wp--preset--font-size--section-header);
  line-height: 1.25;
  border: 2px solid var(--wp--preset--color--tertiary);
  border: solid 2px;
  border-radius: 20px;
  padding: 25px;
  position: relative;
}
.pattern-blockquote p::after {
  content: "";
  position: absolute;
  border: 2px solid var(--wp--preset--color--secondary);
  border-radius: 0 50px 0 0;
  width: 60px;
  height: 60px;
  bottom: -62px;
  left: 50px;
  border-bottom: none;
  border-left: none;
  z-index: 3;
}
.pattern-blockquote p::before {
  content: "";
  position: absolute;
  width: 80px;
  border: 6px solid var(--wp--preset--color--primary);
  bottom: -3px;
  left: 50px;
  z-index: 2;
}
.pattern-blockquote p strong {
  color: var(--wp--preset--color--tertiary);
}
.pattern-blockquote cite {
  position: relative;
  color: var(--wp--preset--color--secondary);
  font-size: 1.3rem;
  font-weight: 400;
  line-height: 1.2;
  margin: 0;
  padding-top: 15px;
  z-index: 1;
  margin-left: 150px;
  padding-left: 12px;
  font-size: var(--wp--preset--font-size--base);
}
.pattern-blockquote cite:first-letter {
  margin-left: -12px;
}
.pattern-blockquote.has-text-align-left {
  float: left;
}
@media (max-width: 768px) {
  .pattern-blockquote.has-text-align-left {
    float: none;
  }
}
.pattern-blockquote.has-text-align-right {
  float: right;
  text-align: left;
}
@media (max-width: 768px) {
  .pattern-blockquote.has-text-align-right {
    float: none;
  }
}

.pattern-cards .wp-block-post-template .wp-block-post {
  transform: translateY(0);
  transition: all 0.3s cubic-bezier(0.18, 0.89, 0.32, 1.28);
}
.pattern-cards .wp-block-post-template .wp-block-post:hover {
  box-shadow: var(--wp--preset--color--secondary) 0px 10px 10px -10px;
  transform: translateY(-10px);
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group {
  display: flex;
  min-height: 248px;
  border-radius: 4px;
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group > .wp-block-group {
  align-content: space-between;
  display: flex;
  position: relative;
  width: 100%;
  margin-top: 0;
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group > .wp-block-group:first-child {
  padding: 16px;
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group .wp-block-post-title {
  padding: 0;
  width: 100%;
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group .wp-block-post-title a {
  color: var(--wp--preset--color--primary);
  font-family: var(--wp--preset--font-family--inter);
  font-size: 1.17em;
  text-decoration: none;
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group .wp-block-post-terms {
  position: absolute;
  z-index: 10;
  right: 8px;
  top: 16px;
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group .wp-block-post-terms a {
  background-color: rgba(29, 29, 29, 0.8);
  padding: 4px 8px;
  font-family: var(--wp--preset--font-family--inter);
  font-size: 16px;
  border-bottom-left-radius: 8px;
  border-top-right-radius: 8px;
  text-decoration: none;
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group .wp-block-post-featured-image,
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group img {
  height: 100%;
  object-fit: cover;
  width: 100%;
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group .pattern-card-author {
  align-items: center;
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group .pattern-card-author .wp-block-post-author__avatar .avatar {
  border-radius: 9999px;
}
.pattern-cards .wp-block-post-template .wp-block-post .pattern-group .pattern-card-author .wp-block-post-author__name {
  color: var(--wp--preset--color--primary);
}

.wp-block-columns.pattern-headline-with-cards .pattern-headline-col:nth-child(2) {
  display: flex;
  align-items: center;
}
.wp-block-columns.pattern-headline-with-cards .pattern-headline-content .has-section-header-font-size {
  font-family: var(--wp--preset--font-family--inter-bold);
}

.wp-block-columns.pattern-headline-with-video .wp-block-column {
  align-items: center;
}
.wp-block-columns.pattern-headline-with-video .pattern-headline-content .has-section-header-font-size {
  font-family: var(--wp--preset--font-family--inter-bold);
}

.pattern-page-hero .page-hero-meta .wp-block-post-excerpt__excerpt {
  font-size: var(--wp--preset--font-size--section-header);
  font-family: var(--wp--preset--font-family--inter-black);
  line-height: 1.25;
}
.pattern-page-hero .page-hero-feature-image .wp-block-post-featured-image {
  height: 100%;
}
.pattern-page-hero .page-hero-feature-image .wp-block-post-featured-image img {
  height: 100%;
  object-fit: cover;
}